/* Create extendable styles for a flex-styled list, horizontal by default. */
.app-main-nav ul, .things-I-love, .favorite-colors {
  display: flex;
  margin: 1rem -0.5rem;
  padding: 0; }
  .app-main-nav ul li, .things-I-love li, .favorite-colors li {
    display: unset;
    flex: 1 1 auto;
    margin: 0.5rem;
    padding: 0 1rem;
    text-align: center; }

