/* More general layout styling */
#root > section {
  padding: 5%; }

/* Styles for the app header */
.app-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }
  .app-header h1 {
    margin-bottom: 0; }
    .app-header h1 + p {
      margin-top: 0.5rem; }
  .app-header a {
    color: white; }

/* Styles specific to the about section */
.things-I-love {
  flex-wrap: wrap; }
  .things-I-love li {
    background: #efefef;
    padding: 2rem 1.5rem; }

.favorite-colors {
  flex-wrap: wrap; }
  .favorite-colors li {
    padding: 1.5rem 2rem; }
    .favorite-colors li:hover h4,
    .favorite-colors li:hover p {
      opacity: 1; }
  .favorite-colors h4 {
    text-transform: capitalize; }
  .favorite-colors h4,
  .favorite-colors p {
    margin: 0.8rem 0;
    opacity: 0;
    -webkit-transition: opacity 300ms ease;
    transition: opacity 300ms ease; }

/* General/simple classes */
.large {
  font-size: 165%;
  line-height: 0;
  vertical-align: sub; }

